.about-us {

  &__block {
    @include onTablet {
      margin: 0 -30px;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: auto auto 1fr;
      gap: 0 20px;
    }

    @include onDesktop {
      gap: 0 30px;
    }
  }

  &__link {
    display: block;
    max-width: fit-content;
    grid-column: 2 / 3;
    grid-row: 3 / 4;

    text-decoration: none;
    color: #0db2b3;
    font-weight: 700;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;

    &--luna-eye {
      grid-column: 1 / 2;
    }

    &::after {
      content: "";
      display: block;
      transform: translateY(7px);
      height: 1px;
      width: 64px;
      background-color: #0db2b3;

      transition: width 0.3s ease;
    }

    &:hover::after {
      width: 100%;
    }

    @include onTablet {
      margin-bottom: 40px;
    }
  }

  &__info {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    margin-bottom: 30px;
    font-size: 14px;
    line-height: 21px;
    color: #7c7c7c;

    &--luna-eye {
      grid-column: 1 / 2;
    }

    @include onTablet {
      margin-bottom: 60px;
      font-size: 16px;
      line-height: 24px;
    }
  }

  &__title {
    font-weight: 700;
    font-size: 36px;
    line-height: 46px;
    text-transform: uppercase;
    color: #333;

    grid-column: 2 / 3;
    grid-row: 1 / 2;

    @include onDesktop {
      font-size: 56px;
      line-height: 70px;
    }

    &--luna-eye {
      grid-column: 1 / 2;
    }
  }

  &__picture {
    grid-column: 1 / 2;
    grid-row: 1 / 4;
    margin-bottom: 20px;
    overflow: hidden;

    &--luna-eye {
      grid-column: 2 / 3;
    }
  }

  &__image {
    width: 100%;
    height: 100%;
    object-fit: cover;

    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.2);
    }
  }
}
