.benefit {

  &__content {
    margin: 0 auto;
    display: none;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 30px;
    overflow-x: hidden;

    @include onTablet {
      display: flex;
      max-width: 640px;
    }
    @include onDesktop {
      display: flex;
      flex-wrap: nowrap;
      max-width: 1020px;
    }
  }

  &__item {
    margin-top: 10px;
    flex-grow: 1;
    width: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    @include onTablet {
      min-width: 200px;
    }

  }
  &__img {
    height: 50px;
    width: 50px;
    margin-bottom: 34px;

    transition: transform 0.3s ease;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__title {
    text-align: center;
    font-weight: 700;
    font-size: 22px;
    line-height: 28px;
    text-transform: uppercase;
    color: #333;

    min-height: 0;
    margin-bottom: 20px;

    @include onTablet {
      margin-bottom: 20px;
      min-height: 56px;
    }
    @include onDesktop {
      margin-bottom: 52px;
      min-height: 56px;
    }
  }
  &__text {
    font-size: 14px;
    line-height: 21px;
    text-align: center;
    color: gray;
  }
}
