.page {
  scroll-behavior: smooth;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #333;
  max-width: 100%;
  overflow-x: hidden;
  
  &__body {
    margin: 0;
    background: #f7f7f7;
    max-width: 100%;
    overflow-x: hidden;

    &--menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    transform: translateX(-100%);
    transition: transform 0.5s linear;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 110px;

    @include onTablet {
      margin-bottom: 140px;
    }
    @include onDesktop {
      margin-bottom: 170px;
    }
  }
}
