.button {
  outline: none;
  display: inline-block;
  text-transform: uppercase;
  border-color: transparent;
  border-radius: 15px;
  cursor: pointer;

  transition: opacity 0.3s ease;

  &__slider {
    width: 50px;
    position: absolute;
    top: 40%;
    bottom: 40%;

    font-weight: 700;
    color: #333;

    &--prev {
      right: 0;
    }
    &--next {
      left: 0;
    }

    transition: opacity 0.3s ease;

    &:hover {
      color: #0db2b3;
      opacity: 0.5;
    }

    &-text {
      &--prev {
        transform-origin: 50% ;
        transform: rotate(90deg);
      }
      &--next {
        transform: rotate(-90deg);
      }
    }
  }
}
