.benefit-mob {
  display: block;
  position: relative;
  padding-bottom: 400px;

  @include onTabletAndDesktop {
    display: none;
  }

  &__content {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;

    min-width: 200px;
    min-height: 430px;

    margin: 0 -30px;

    background-color: #f7f7f7;

  }
  &__item {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;

    min-width: 200px;
    padding: 35px 65px;

    border-radius: 5px;
    opacity: 0;

    transition: opacity 0.3s ease;
  }

  &__pagination {
    position: absolute;
    bottom: 5px;
    right: 50%;
    transform: translateX(50%);
    list-style: none;

    display: flex;
    justify-content: center;
    padding-bottom: 5px;

    & > :not(:last-child) {
      margin-right: 30px;
    }
  }

  &__pag {
    display: block;
    
    height: 7px;
    width: 7px;
    border-radius: 50%;
    background-color: white;
  }
}
