.picture {
  margin: 0 -30px;
  display: flex;
  justify-content: center;

  mix-blend-mode: darken;

  opacity: 0.7;
  transition: opacity 0.3s ease , transform 0.3s ease;
  
  @include onTabletAndDesktop {
    margin: 0;
  }
  
  &__img {
    width: 100%;
    object-fit: cover;
  }

  &:hover {
    opacity: 1;
    transform: scale(1.1);
  }
}
